.home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -160px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home {
    max-width: 1500px;
    margin: 0 auto;
}

.home__row {
    display: flex;
    z-index: 1;
    margin: 0 5px;
    overflow-x: scroll;
}

.home__row::-webkit-scrollbar {
    display: none;
}